/* About Us Section Styles */
.about-us {
    background-color: #fff;
    color: #333;
    padding: 10rem 2rem 4rem 2rem;
    text-align: center;
  }
  
  .about-us-content {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .about-us h2 {
    font-size: 2.5rem;
    color: #f4a261; /* Same as button color from Hero section */
    margin-bottom: 1rem;
  }
  
  .about-us p {
    font-size: 1.25rem;
    line-height: 1.6;
    margin-bottom: 2rem;
    color: #333;
  }
  
  .about-us-button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    background-color: #f4a261; /* Same color as Hero button */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .about-us-button:hover {
    background-color: #e76f51; /* Same hover effect as Hero button */
  }

  .team-member {
    width: 30%;
    margin: auto;
  }

  .team-member img {
    width: 100%;
  }
  
  @media screen and (max-width: 750px) {
    .about-us {
      padding: 4rem 2rem;
      margin-top: 80px;
    }
    
    .about-us p {
      font-size: 1rem;
    }

    .team-member {
      width: 270px;
    }
}