.contact-us {
  padding: 4rem 2rem;
  text-align: center;
}

.contact-us h2 {
  margin-top: 4rem;
  margin-bottom: 1rem;
  font-size: 2.5rem;
  color: #f4a261;
}

.contact-us p {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 2rem;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
  background: #f9f9f9;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  width: 100%;
  padding: 12px;
  background-color: #f4a261;
  color: white;
  font-size: 1.1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #e76f51;
}

.contact-info {
  margin-top: 3rem;
}

.contact-info h3 {
  font-size: 1.8rem;
  color: #f4a261;
  margin-bottom: 1rem;
}

.contact-info p {
  font-size: 1.1rem;
  color: #333;
}

.social-links a {
  color: #f4a261;
  font-size: 1.2rem;
  margin: 0 10px;
  text-decoration: none;
}

.social-links a:hover {
  color: #e76f51;
}

iframe {
  margin: 2rem auto;
  height: 500px;
  width: 80%;
}

@media screen and (max-width: 750px) {
  .contact-us h2 {
    margin-top: -4rem;
  }

  iframe {
    margin: 2rem auto;
    width: 60%;
  }
}

@media screen and (max-width: 450px) {

  iframe {
    width: 100%;
    height: 300px;
  }
}
